<template>
	<view class="container">
		<!-- #ifdef MP-WEIXIN -->
		<!-- <tuicalling
		ref="TUICalling"
		id="TUICalling-component"
		:config="config">
	</tuicalling> -->
		<!-- #endif -->
		<view class="tui-chatroom-navigatorbar">
			<image class="tui-chatroom-navigatorbar-back" src="/static/back-left.png" @tap="goBack" />
			<!-- 先查 remark；无 remark 查 (c2c)nick/(group)name；最后查 (c2c)userID/(group)groupID -->
			<view class="conversation-title">{{ conversationName }}</view>
		</view>
		<view class="column" style="width: 750rpx;background-color: white;" v-if="isOrder">
			<view class="" style="font-size: 16px;margin-top: 5px;margin-left: 15px;">
				外卖小哥看到消息会第一时间回复
			</view>
			<view class="" style="font-size: 12px;margin-top: 5px;margin-left: 15px;">
				{{orderMSg.ads}}
			</view>
			<view class="row"
				style="width: 100%;background-color: #ECEAF2;height: 32px;margin-top: 10px;align-items: center;">
				<image src="../../static/msg-order.png" mode="" style="margin-left: 15px;width: 20px;height: 20px;">
				</image>
				<view style="font-size: 14px;color:#FF5725;margin-left:8px;">
					订单：{{orderMSg.ordernum}}
				</view>
			</view>
		</view>
		<view class="group-profile">
			<TUI-group-profile v-if="isShow" id="groip-profile" :conversation="conversation" />
		</view>
		<view class="message-list" @tap="triggerClose">
			<TUI-message-list id="message-list" ref="messageList" :conversation="conversation" />
		</view>
		<view v-if="videoPlay" class="container-box" @tap.stop="stopVideoHander">
			<video v-if="videoPlay" class="video-message" :src="videoMessage.payload.videoUrl"
				:poster="videoMessage.payload.thumbUrl" object-fit="cover" error="videoError" autoplay="true"
				direction="0" />
		</view>
		<view v-if="showChat" class="message-input">
			<TUI-message-input id="message-input" ref="messageInput" :conversation="conversation"
				@sendMessage="sendMessage" />
		</view>
	</view>
</template>

<script>
	import logger from '../../utils/logger';
	import TUIMessageList from '../../components/tui-chat/message-list/index';
	import TUIMessageInput from '../../components/tui-chat/message-input/index';

	const app = getApp();
	export default {
		components: {
			TUIMessageList,
			TUIMessageInput,
		},
		props: {},
		data() {
			return {
				conversationName: '',
				conversation: {},
				messageList: [],
				isShow: false,
				showChat: true,
				conversationID: '',
				videoPlay: false,
				videoMessage: {},
				config: {
					sdkAppID: '',
					userID: '',
					userSig: '',
					type: 1,
					tim: null
				},
				isOrder: false,
				orderMSg: null,
				// toSignature: '',
				// selfnickname: ''
			};
		},
		created() {
			uni.$on('videoPlayerHandler', value => {
				this.videoPlay = value.isPlay;
				this.videoMessage = value.message;
			});

		},

		/**
		 * 生命周期函数--监听页面加载
		 */
		onLoad(options) {
			// conversationID: C2C、 GROUP
			const {
				conversationID
			} = options;
			this.setData({
				conversationID
			});
			logger.log(` TUI-chat | onLoad | conversationID: ${conversationID}`);

			// #ifdef MP-WEIXIN
			// this.config = {
			// 	sdkAppID: app.globalData.SDKAppID,
			// 	userID: app.globalData.userInfo.userID,
			// 	userSig: app.globalData.userInfo.userSig
			// }
			// logger.log(`TUI-chat | TUICalling-config  | config:${JSON.stringify(this.callingConfig)}`);
			// // 挂载在 uni 上
			// uni.$wxTUICalling = this.$refs.TUICalling;
			// 		this.$nextTick(() => {
			// 			uni.$wxTUICalling.init()
			// })
			// #endif
			uni.$TUIKit
				.setMessageRead({
					conversationID
				})
				.then(() => {
					logger.log('TUI-chat | setMessageRead  | ok');
				});
			uni.$TUIKit.getConversationProfile(conversationID).then(res => {
				const {
					conversation
				} = res.data;
				this.conversation = conversation;
				console.log(conversation, conversation.userProfile.selfSignature)
				this.setData({
					conversationName: this.getConversationName(conversation),
					isShow: conversation.type === 'GROUP',
					// toSignature: conversation.userProfile.selfSignature
				});
			});
			var that = this
			this.orderMSg = JSON.parse(options.order)
			if (this.orderMSg && this.orderMSg.id) {
				this.isOrder = true
			}
			const value = uni.getStorageSync('userinfo');
			this.selfnickname = value.user_nickname
		},
		mounted() {},
		onUnload() {
			// #ifdef MP-WEIXIN
			//   this.$refs.TUICalling.destroyed();
			// // #endif
		},
		methods: {
			sendOrderMsg(conversationID) {
				// 发送文本消息，Web 端与小程序端相同
				// 1. 创建消息实例，接口返回的实例可以上屏
				let message = uni.$TUIKit.createTextMessage({
					to: conversationID.replace('C2C', ''),
					conversationType: 'C2C',
					// 消息优先级，用于群聊（v2.4.2起支持）。如果某个群的消息超过了频率限制，后台会优先下发高优先级的消息，详细请参考：https://cloud.tencent.com/document/product/269/3663#.E6.B6.88.E6.81.AF.E4.BC.98.E5.85.88.E7.BA.A7.E4.B8.8E.E9.A2.91.E7.8E.87.E6.8E.A7.E5.88.B6)
					// 支持的枚举值：TIM.TYPES.MSG_PRIORITY_HIGH, TIM.TYPES.MSG_PRIORITY_NORMAL（默认）, TIM.TYPES.MSG_PRIORITY_LOW, TIM.TYPES.MSG_PRIORITY_LOWEST
					// priority: TIM.TYPES.MSG_PRIORITY_NORMAL,
					payload: {
						text: 'TUIKit-kami' + this.orderMSg.id
					},
					// v2.20.0起支持C2C消息已读回执功能，如果您发消息需要已读回执，需购买旗舰版套餐，并且创建消息时将 needReadReceipt 设置为 true
					needReadReceipt: false
					// 消息自定义数据（云端保存，会发送到对端，程序卸载重装后还能拉取到，v2.10.2起支持）
					// cloudCustomData: 'your cloud custom data'
				});
				// 2. 发送消息
				let promise = uni.$TUIKit.sendMessage(message);
				promise.then(function(imResponse) {
					// 发送成功
					console.log(imResponse);
				}).catch(function(imError) {
					// 发送失败
					console.warn('sendMessage error:', imError);
				});
			},
			stopVideoHander() {
				this.videoPlay = false;
			},
			getConversationName(conversation) {
				if (conversation.type === '@TIM#SYSTEM') {
					this.setData({
						showChat: false
					});
					return '系统通知';
				}

				if (conversation.type === 'C2C') {
					return conversation.remark || conversation.userProfile.nick || conversation.userProfile.userID;
				}

				if (conversation.type === 'GROUP') {
					return conversation.groupProfile.name || conversation.groupProfile.groupID;
				}
			},

			sendMessage(event) {
				console.log(event, event.detail.message.payload.text)
				//event.detail.message.payload.text
				// 将自己发送的消息写进消息列表里面
				this.$refs.messageList.updateMessageList(event.detail.message);
				// console.log(this.toSignature)
				// this.toSignature = 'b88efc49b8a156418bcffb9bef452de1'
				// if (this.toSignature.length > 0) {
				this.sendnotifacation(event.detail.message.payload.text)
				// }
			},
			sendnotifacation(text) {
				var that = this
				that.http.sendRequest('Push.Send', {
					'to':that.conversationID.replace('C2C', ''),
					'content':text
				}).then(function(info) {
					
				})
			},
			// 32位随机数
			randomString(length) {
				let str = 'ABCDEFGHIJKIMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'
				let num = '';
				for (let i = 0; i < 32; i++) {
					num += str.charAt(Math.floor(Math.random() * str.length));
				}
				console.log(num, 'num')
			},
			triggerClose() {
				if (this.showChat) {
					this.$refs.messageInput.handleClose();
				}
			},

			goBack() {
				const pages = getCurrentPages(); // 当前页面栈

				if (
					pages[pages.length - 2].route === 'pages/TUI-Conversation/create-conversation/create' ||
					pages[pages.length - 2].route === 'pages/TUI-Group/create-group/create' ||
					pages[pages.length - 2].route === 'pages/TUI-Group/join-group/join'
				) {
					uni.navigateBack({
						delta: 2
					});
				} else {
					uni.navigateBack({
						delta: 1
					});
				}

				uni.$TUIKit
					.setMessageRead({
						conversationID: this.conversationID
					})
					.then(() => {});
			}
		}
	};
</script>
<style>
	@import './chat.css';
</style>
